import React, { useCallback, useState, useEffect } from 'react';
import { Input, Form, Button } from "antd";
import { useSelector, useDispatch } from 'react-redux';
import { ADD_POST_REQUEST} from '../reducers/post';
const PostForm = () => {
  const dispatch = useDispatch();
  const [text, setText] = useState('');
  const { imagePaths, isAddingPost } = useSelector(state => state.post);
  const onsubmitForm = useCallback(()=>{
    dispatch({
      type: ADD_POST_REQUEST,
      data:{
        text,
      },
    });
  },[]);
  const onChangeText= useCallback((e)=>{
    estText(e.target.value);
  },[]);
    return (
        <Form style={{ margin: '10px 0 20 px' }} encType="multipart/form-data" onSubmit={onsubmitForm}>
        <Input.TextArea
          maxLength={140}
          placeholder="어떤 신기한 일이 있었나요?"
          value={text}
          onChange={onChangeText}
        ></Input.TextArea>
        <div>
          <Button>이미지 업로드</Button>
          <Button
            type="primary"
            style={{ float: "right" }}
            htmlType="submit"
            isLoading={isAddingPost}
          >
            짹짹
          </Button>
        </div>
        <div>
          {imagePaths.map((v) => {
            return (
              <div key={v} style={{ display: "inline-block" }}>
                <img
                  src={"http://localhost:3065/"}
                  style={{ width: "200px" }}
                  alt={v}
                ></img>
                <div>
                  <Button>제거</Button>
                </div>
              </div>
            );
          })}
        </div>
      </Form>
    )
}

export default PostForm;